#home_container {
      width: 100vw;
      height: 100vh;
      display: flex;
      text-align: right;
      justify-content: flex-end;
      align-content: right;
      align-items: center;
}


h2 {
      text-shadow:#b08338 0.042em -0.042em;
}

h6 {
      color: #ffc159;
}


.header_title {
      color: #5ee2c3;
      white-space: nowrap;
      font-weight: 300;
      font-stretch: normal;
      font-size: 16px;
      line-height: 1.5;
      font-weight: 300;
      padding: 0 10% 0% 10%;
      margin: 16px;
      height: auto;
}

#home_background_animation {
      position: absolute;
      width: 100vw;
      height: 100vh;
      z-index: -1;
}

@media screen and (min-width: 0px) and (max-width: 299px) {


      h2 {
            font-size: 20px;
            font-weight: 350;
      }


      h6 {
            font-size: 14px;
            font-weight: 100;
      }


}

@media screen and (min-width: 300px) and (max-width: 399px) {

      h2 {
            font-size: 25px;
            font-weight: 350;
      }


      h6 {
            font-size: 17px;
            font-weight: 100;
      }

}

