      }

}

@media screen and (min-width: 700px) and (max-width: 849px) {


      h2 {
            font-size: 50px;
            font-weight: 350;
      }


      h6 {
            font-size: 30px;
            font-weight: 100;
      }

}

@media screen and (min-width: 850px) and (max-width: 999px) {


      h2 {
            font-size: 60px;
            font-weight: 350;
      }


      h6 {
            font-size: 40px;
            font-weight: 100;
      }

}

@media screen and (min-width: 1000px) and (max-width: 1399px) {

      h2 {
